@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@600&family=IBM+Plex+Sans:wght@300;400&display=swap);
*,
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "IBM Plex Sans", sans-serif;
  font-family: var(--body-font-family);
  font-size: 16px;
  line-height: 1.5;
  font-weight: 300;
  background-color: #f7f5f2;
  background-color: var(--body-bg);
  color: #35403a;
  color: var(--dark-grey);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Bitstream Vera Sans Mono", "Courier", monospace;
  font-family: var(--code-font);
}

a {
  color: #d9965b;
  color: var(--brand-primary);
  font-weight: 400;
}

h1,
h2,
h3,
h4,
p {
  margin-top: 0;
}

h1,
h2,
h3,
h4 {
  font-family: "Cormorant Garamond", serif;
  font-family: var(--headings-font-family);
  color: #414042;
  color: var(--grey-mid);
  font-weight: 600;
}

h2 {
  font-size: 2rem;
}

p,
li {
  font-size: 1rem;
}

@media (min-width: 768px) {
  p,
  li {
    font-size: 1.25rem;
  }
}

section {
  padding: 2rem 0;
  width: 90%;
  margin: 0 auto;
}

.dark-bg {
  background-color: rgba(217, 150, 91, 0.1);
  background-color: var(--brand-primary-rgba);
}

@media (min-width: 768px) {
  section {
    padding: 4rem 0;
    width: 70%;
  }
}

.button {
  display: inline-block;
  color: #d9965b;
  color: var(--brand-primary);
  border: 1px solid #d9965b;
  border: 1px solid var(--brand-primary);
  border-radius: 4px;
  text-decoration: none;
  padding: 1.25rem 1.75rem;
  transition: all 0.3s ease;
}

.button:hover {
  background-color: rgba(217, 150, 91, 0.1);
  background-color: var(--brand-primary-rgba);
}

/*
// VARIABLES
*/
:root {
  /* Colors */
  --brand-primary: #d9965b;
  --black: #000;
  --white: #fff;
  --body-bg: #f7f5f2;

  /* Greys */
  --dark-grey: #35403a;
  --grey-mid: #414042;
  --grey-light: #6c6c6c;

  /* RGBA */
  --brand-primary-rgba: rgba(217, 150, 91, 0.1);
  --grey-mid-rgba: rgba(65, 64, 66, 0.1);

  /* Font stacks */
  --headings-font-family: "Cormorant Garamond", serif;
  --body-font-family: "IBM Plex Sans", sans-serif;
  --code-font: "Bitstream Vera Sans Mono", "Courier", monospace;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

@media (min-width: 768px) {
  header {
    padding: 1rem 2.5rem;
  }
}

nav a {
  display: inline-block;
  padding: 0.5rem 0;
  margin: 0.5rem;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

nav a:hover {
  opacity: 0.8;
  border-bottom: 1px solid var(--brand-primary);
}

.resume-button {
  display: none;
}

@media (min-width: 768px) {
  nav a {
    margin: 0.5rem 1rem;
  }

  .resume-button {
    display: inline-block;
    padding: 0.75rem 1rem;
  }
}

.intro {
  min-height: 80vh;
  max-width: 90%;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.heading {
  padding: 0;
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

@media (min-width: 768px) {
  .heading {
    font-size: 3.5rem;
    margin-bottom: 2rem;
  }
}

.sub-heading {
  color: var(--grey-light);
  display: block;
}

.intro-about {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  max-width: 500px;
}

@media (min-width: 768px) {
  .intro-about {
    margin-bottom: 3rem;
  }
}

.intro-about p {
  color: var(--grey-light);
}

.about-section p {
  max-width: 600px;
}

.about-section ul {
  max-width: 500px;
  margin: 0;
  padding: 0;
  -webkit-columns: 2;
          columns: 2;
  list-style: none;
}

.about-section li::before {
  display: inline-block;
  content: "►";
  font-size: 0.8rem;
  padding-right: 0.5rem;
  color: var(--brand-primary);
}

.tabs {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .tabs {
    flex-direction: row;
  }
}

.tab-list {
  list-style: none;
  margin: 0 0 1rem 0;
  padding: 0;
  display: flex;
  overflow-x: scroll;
  margin-left: -1rem;
}

@media (min-width: 768px) {
  .tab-list {
    display: block;
    overflow-x: auto;
    margin-left: 0;
  }
}

.tab-link {
  cursor: pointer;
  display: inline-block;
  padding: 0.5rem 1rem;
  text-decoration: none;
  background-color: transparent;
  border-bottom: 1px solid transparent;
  transition: all ease 0.3s;
  white-space: nowrap;
}

@media (min-width: 768px) {
  .tab-link {
    display: block;
    border-bottom: 0;
    border-left: 3px solid transparent;
    white-space: normal;
  }

  .tab-link:hover {
    border-bottom: 0;
    border-left: 3px solid var(--brand-primary);
  }
}

.tab-link--active {
  background-color: var(--brand-primary-rgba);
  border-bottom: 1px solid var(--brand-primary);
}

@media (min-width: 768px) {
  .tab-link--active {
    border-bottom: 0;
    border-left: 3px solid var(--brand-primary);
  }
}

.tab-content {
  padding: 0.5rem 1rem;
  transition: all ease 0.3s;
}

@media (min-width: 768px) {
  .tab-content {
    width: 80%;
    padding: 0.5rem 1rem 0.5rem 2rem;
  }
}

.tab-content p {
  font-size: 1rem;
  color: var(--grey-light);
}

.tab-content ul {
  padding: 0;
  list-style: none;
}

.tab-content li::before {
  display: inline-block;
  content: "►";
  padding-right: 0.5rem;
  font-size: 0.8rem;
  color: var(--brand-primary);
}

.contact-section p {
  max-width: 600px;
}

.contact-section p + p {
  margin-bottom: 3rem;
}

